import type React from "react";
import { ListItem, ListItemText, Typography, Box, useTheme, Button } from "@mui/material";
import { LoadedGame } from "@/types/game";
import TimeControlChip from "./timeControlChip";
import MovesNbChip from "./movesNbChip";
import DateChip from "./dateChip";
import GameResultChip from "./gameResultChip";
import { GameOrigin } from "@/types/enums";
import GameLinkHelper from "@/lib/gameLinkHelper";
import { toast } from "react-toastify";
interface Props {
  game: LoadedGame;
  onClick: () => void;
  perspectiveUserColor: "white" | "black";
}
export const GameItem: React.FC<Props> = ({
  game,
  onClick,
  perspectiveUserColor
}) => {
  const theme = useTheme();
  const {
    white,
    black,
    result,
    timeControl,
    date,
    movesNb
  } = game;
  const whiteWon = result === "1-0";
  const blackWon = result === "0-1";
  return <ListItem alignItems="flex-start" sx={{
    borderRadius: 2,
    mb: 1.5,
    transition: "all 0.2s ease-in-out",
    "&:hover": {
      backgroundColor: theme.palette.action.hover,
      boxShadow: theme.shadows[3]
    },
    border: `1px solid ${theme.palette.divider}`,
    cursor: "pointer"
  }} onClick={onClick} data-sentry-element="ListItem" data-sentry-component="GameItem" data-sentry-source-file="index.tsx">
      <ListItemText disableTypography primary={<Box sx={{
      display: "flex",
      alignItems: "center",
      gap: {
        xs: 1,
        sm: 1.5
      },
      mb: 1
    }}>
            <Typography variant="subtitle1" component="span" noWrap sx={{
        fontWeight: "700",
        color: whiteWon ? theme.palette.success.main : theme.palette.text.primary,
        opacity: whiteWon ? 1 : blackWon ? 0.7 : 0.8
      }}>
              {formatPlayerName(white)} ({white.rating})
            </Typography>

            <Typography variant="body2" component="span" sx={{
        color: theme.palette.text.secondary,
        fontWeight: "500"
      }}>
              vs
            </Typography>

            <Typography variant="subtitle1" component="span" noWrap sx={{
        fontWeight: "700",
        color: blackWon ? theme.palette.success.main : theme.palette.text.primary,
        opacity: blackWon ? 1 : whiteWon ? 0.7 : 0.8
      }}>
              {formatPlayerName(black)} ({black.rating})
            </Typography>

            <GameResultChip result={result} perspectiveUserColor={perspectiveUserColor} />
            
            {game.url?.includes("www.chess.com") && <Button variant="contained" onClick={event => {
        event.stopPropagation();
        let siteName: string = "";
        let gameId: string = "";
        let username: string = "";
        if (game.url?.includes("www.chess.com")) {
          siteName = GameOrigin.ChessCom;
          const splitUrl = game.url?.split("/");
          gameId = splitUrl?.at(-1) ?? "";
          switch (perspectiveUserColor) {
            case "white":
              username = game.white.name;
              break;
            case "black":
              username = game.black.name;
              break;
          }
          const gameLinkToCopy = GameLinkHelper.generateGameUrl(siteName, gameId, username);
          navigator.clipboard.writeText(gameLinkToCopy).then(() => {
            toast.success("Successfully copied link", {
              theme: "dark",
              autoClose: 2000
            });
          }).catch(err => {
            toast.warning(`Error copying link: ${err}`);
          });
        }

        //TODO: Add support for lichess
      }}>
                Copy Game Link
              </Button>}
          </Box>} secondary={<Box sx={{
      display: "flex",
      gap: 1,
      alignItems: "center"
    }}>
            <TimeControlChip timeControl={timeControl} />
            <MovesNbChip movesNb={movesNb} />
            <DateChip date={date} />
          </Box>} data-sentry-element="ListItemText" data-sentry-source-file="index.tsx" />
    </ListItem>;
};
const formatPlayerName = (player: LoadedGame["white"]) => {
  return player.title ? `${player.title} ${player.name}` : player.name;
};